<template>
  <div class="paying">
    <p>支付中...</p>
    <img :src="`${baseUrl}paying.png`" alt="">
  </div>
</template>

<script>
export default {
  name: 'PayLoading',
  components: {
  },
  data () {
    return {
      timer: null
    }
  },
  created () {
    this.checkOrderInWx()
    let count = 10
    this.timer = setInterval(() => {
      if (count <= 0) {
        clearInterval(this.timer)
        return
      }
      count--
      this.checkOrderInWx()
    }, 1000)
  },
  beforeDestroy () {
    clearInterval(this.timer)
  },
  methods: {
    checkOrderInWx () {
      return this.$apis.payment
        .checkOrderInWx({ product_sn: 'PRODUCT_SN_CQ_7' })
        .catch((err) => {
          if (err.code === 30009) {
            this.$router.push({
              name: 'ChangqingCode',
              query: {
                ...this.$route.query,
                productSn: 'PRODUCT_SN_CQ_7'
              }
            })
          }
        })
    }
  }
}
</script>

<style lang="scss" scoped>
.paying {
  padding-top: 200px;

  @include verCenter();

  flex-direction: column;

  p {
    font-size: 38px;
    color: #333;
  }

  img {
    width: 538px;
    height: 314px;
    margin-top: 100px;
  }
}
</style>
